.recent .text {
    padding: 30px;
}
.recent .category span{
    padding: 4px 15px;
    border-radius: 2px;
    font-weight: 600;
    font-size: 13px;
    display: inline-block;
}
.recent .category i {
    font-size: 20px;
    color: #bec7d8;
}
.recent p {
    color: #72809d;
}
.recent .button{
    border-top: 1px solid #efe8f3;
    padding: 10px 30px;
}
@media (max-width:768px) {
    .recent .container {
        display: flex;
        flex-direction: column;
    }
}